.base {
    margin-top: 5%;
    border: 0.00000000rem solid black;
    -webkit-box-shadow: 0px 0px 14px -1px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 14px -1px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 14px -1px rgba(0,0,0,0.75);
    padding: 2rem;
}

.main-title {
    background-color: #242582;
    text-align: center;
    margin-top: -3rem;
    color: #fff;
    width: 50%;
    margin-left: 25%;
    border-radius: 0.5rem;
}

.input-section {
    margin-top: 1rem;
    display: flex;
    flex-wrap: wrap;
}

.total {
    text-align: center;
    margin-top: 1rem;
    font-size: 4rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.error {
    background-color: #bb0000;
    width: 50%;
    margin-left: 25%;
    color: #fff;
    text-align: center;
    padding: 0.1rem;
    border-radius: 0.2rem;
}